export default "import React from 'react';\r\nimport {\r\n  CartesianGrid,\r\n  Legend,\r\n  Line,\r\n  LineChart,\r\n  ReferenceLine,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport data from './data';\r\n\r\nconst LineChartWithReferenceLines = () => (\r\n  <ResponsiveContainer width='100%' height={200}>\r\n    <LineChart data={data} margin={{top: 10, right: 0, left: -25, bottom: 0}}>\r\n      <XAxis dataKey='name' />\r\n      <YAxis />\r\n      <CartesianGrid strokeDasharray='3 3' />\r\n      <Tooltip />\r\n      <Legend />\r\n      <ReferenceLine x='Page C' stroke='#dc3545' label='Max PV PAGE' />\r\n      <ReferenceLine y={9800} label='Max' stroke='#dc3545' />\r\n      <Line type='monotone' dataKey='pv' stroke='#4299E1' />\r\n      <Line type='monotone' dataKey='uv' stroke='#F04F47' />\r\n    </LineChart>\r\n  </ResponsiveContainer>\r\n);\r\n\r\nexport default LineChartWithReferenceLines;\r\n";